.wrapper{
  position: absolute;
  z-index: 999;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 260px;
  height: 260px;
  img{
    width: 100%;
    height: 100%;
  }
}
.overlay{
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #EDF2F7;
  opacity: 0.4;
}